<template>
    <div>
        <div class="quizInfo" v-if="infoShow">
            <p class="display-3"> .</p>
            <p class="body-2"> 10 ,     .</p>
            <v-btn color="success" v-on:click="runQuiz">Start</v-btn>
        </div>
        <div class="quizTemplate" v-if="quizShow">
            <v-progress-circular class="timer" :rotate="-90" :size="100" :width="15" :value="timerValue" color="green accent-2">
                {{ timerValue/10 }}
            </v-progress-circular>
            <div class="questionAndAnswer">
                <div class="questionBox">
                    <p class="question display-1">{{questionFront}}
                        <v-btn outline disabled></v-btn>
                        {{questionEnd}}</p>
                </div>
                <div class="answerBox">
                    <v-btn outline v-on:click="answerCheck(0)" v-bind:disabled="done">{{choice0}}</v-btn>
                    <v-btn outline v-on:click="answerCheck(1)" v-bind:disabled="done">{{choice1}}</v-btn>
                    <v-btn outline v-on:click="answerCheck(2)" v-bind:disabled="done">{{choice2}}</v-btn>
                    <v-btn outline v-on:click="answerCheck(3)" v-bind:disabled="done">{{choice3}}</v-btn>
                </div>
            </div>
            <div class="answerAlertArea">
                <v-alert :value="rightAnswerFlag" type="success" transition="scale-transition">
                    .
                </v-alert>
                <v-alert :value="wrongAnswerFlag" type="error" transition="scale-transition">
                    .
                </v-alert>
            </div>
        </div>
        <div class="resultPage display-1" v-if="resultShow">
              {{totalScore}}.
            <v-btn class="retryBtn" color="success" v-on:click="runQuiz"> </v-btn>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            allQuestionsNumber: 10,
            interval: {},
            timerValue: 100,
            infoShow: true,
            quizShow: false,
            questionFront: "",
            questionEnd: "",
            choice0: "",
            choice1: "",
            choice2: "",
            choice3: "",
            answerNum: 0,
            quizCounter: 0,
            rightAnswerFlag: false,
            wrongAnswerFlag: false,
            done: false,
            totalScore: 785,
            resultShow: false
        }
    },
    methods: {
        runQuiz() {
            this.infoShow = false;
            this.quizShow = true;
            this.loadQuiz();
        },
        timerReset() {
            this.timerValue = 100;
            this.interval = setInterval(() => {
                this.timerValue -= 10;
                if (this.timerValue == 0)
                    clearInterval(this.interval);
            }, 1000);
        },
        loadQuiz() {
            this.timerReset();
            axios.get('/me/quiz/set1') //   
                .then(response => {
                    this.questionFront = response.questionFront;
                    this.questionEnd = response.data.questionEnd;
                    this.answerNum = response.data.answerNum;
                    this.choice0 = response.data.choice0;
                    this.choice1 = response.data.choice1;
                    this.choice2 = response.data.choice2;
                    this.choice3 = response.data.choice3;
                });
        },
        answerCheck(userChoicedAnswer) {
            if (userChoicedAnswer == this.answerNum) {
                this.rightAnswerFlag = true;
            } else {
                this.wrongAnswerFlag = true;
            }
            this.done = true;
            this.goNextOrShowResult();
            clearInterval(this.interval);
        },
        toNext() {
            this.done = false,
                this.rightAnswerFlag = false,
                this.wrongAnswerFlag = false,
                this.quizCounter++;
            this.loadQuiz();
        },
        goNextOrShowResult() {
            if (this.allQuestionsNumber != (this.quizCounter + 1)) {
                setTimeout(() => this.toNext(), 1000);
            } else {
                setTimeout(() => this.showResultPage(), 1500);
            }
        },
        showResultPage() {
            this.quizShow = false;
            this.resultShow = true;
            this.allDataReset();
        },
        allDataReset() {
            this.questionFront = "";
            this.questionEnd = "";
            this.choice0 = "";
            this.choice1 = "";
            this.choice2 = "";
            this.choice3 = "";
            this.answerNum = 0;
            this.quizCounter = 0;
            this.rightAnswerFlag = false;
            this.wrongAnswerFlag = false;
            this.done = false;
        }
    }
}
</script>
<style scoped>
.quizInfo {
    text-align: center;
    position: relative;
    top: 200px;
    color:#00C853;
}

.questionAndAnswer {
    position: relative;
    padding: 100px;
    top: 20px;
}

.answerBox {
    margin-top: 60px;
}

.timer {
    position: relative;
    top: 50px;
    left: 1080px;
}

.resultPage {
    position: relative;
    top: 260px;
    text-align: center;
    color: #00C853;
}

.retryBtn {
    display: block;
    position: relative;
    top: 30px;
    left: 550px;
}
</style>