<template>
    <v-dialog v-model="propsdata" persistent max-width="400px">
        <v-card>
            <v-card-title>
                <span class="headline">  ?</span>
            </v-card-title>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red" flat @click.native="onLogout"></v-btn>
                <v-btn color="blue darken-1" flat v-on:click="close"></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script type="text/javascript" scoped>
    export default {
        props: ['propsdata'],
        methods: {
            // . ,  . 
            onLogout() {
                window.location.href = "https://www.google.co.kr/";
            },

            // .
            close(){
                this.$emit('closeLogoutModal')
            }
        }
    }
</script>

